import React, { Component } from 'react';
import propTypes from 'prop-types';
import { Transition, animated } from 'react-spring/renderprops';

class Options extends Component { 
    render() {
        const s = this.props.setSessionTime;
        const b = this.props.setBreakTime;
        const visible = this.props.visible;

        return (
            <Transition
                native
                items={visible}
                from={{height: 0}}
                enter={{height: "auto"}}
                leave={{height: 0}}
                config={{duration: 500}}
            >
                {show => show && (props => (
                    <animated.div id="expanded-options" style={props}>
                            <div id="session-label"><h3>Session</h3></div>
                            <div id="break-label"><h3>Break</h3></div>
                            <button type="button" id="session-increment" onClick={!this.props.isCounting ? this.props.sessionInc : null}><i className="fas fa-caret-up"></i></button>
            <div id="session-length"><p>{`${s[0]}${s[1]}`}</p></div>
                            <button type="button" id="session-decrement" onClick={!this.props.isCounting ? this.props.sessionDec : null}><i className="fas fa-caret-down"></i></button>
                            <button type="button" id="break-increment" onClick={!this.props.isCounting ? this.props.breakInc : null}><i className="fas fa-caret-up"></i></button>
            <div id="break-length"><p>{`${b[0]}${b[1]}`}</p></div>
                            <button type="button" id="break-decrement" onClick={!this.props.isCounting ? this.props.breakDec : null}><i className="fas fa-caret-down"></i></button>
                    </animated.div>
                ))}
                
                
            </Transition>
        )
    }
}

Options.propTypes = {isCounting: propTypes.bool.isRequired, setSessionTime: propTypes.array.isRequired, setBreakTime: propTypes.array.isRequired, visible: propTypes.bool.isRequired, sessionDec: propTypes.func.isRequired, sessionInc: propTypes.func.isRequired, breakDec: propTypes.func.isRequired, breakInc: propTypes.func.isRequired};

export default Options
