import React from 'react';
import Timer from './components/Timer';
import Options from './components/Options';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isCounting: false,
      type: "Session",
      setSessionTime: [2, 5],
      setBreakTime: [0, 5],
      currentSessionTime: [2, 5, 0, 0],
      currentBreakTime: [0, 5, 0, 0],
      visible: false,
      isPaused: true,
      countdown: {}
    }
  }


  visToggle = () => {
    this.setState((state, props) => {
      return { visible: !state.visible }
    })
  }

  breakInc = () => {
    const current = this.state.setBreakTime;
    const first = current[0];
    const second = current[1];
    if (first >= 6) {
      return;
    } else if (second !== 9) {
      this.setState((state, props) => {
        return {
          setBreakTime: [first, second + 1],
          currentBreakTime: [first, second + 1, 0, 0]
        }
      });
    } else {
      this.setState((state, props) => {
        return {
          setBreakTime: [first + 1, 0],
          currentBreakTime: [first + 1, 0, 0, 0]
        }
      })
    }
  }

  breakDec = () => {
    const current = this.state.setBreakTime;
    const first = current[0];
    const second = current[1];
    if (first === 0 && second <= 1) {
      return;
    } else if (second !== 0) {
      this.setState((state, props) => {
        return { setBreakTime: [first, second - 1],
        currentBreakTime: [first, second - 1, 0, 0] }
      });
    } else {
      this.setState((state, props) => {
        return { setBreakTime: [first - 1, 9],
        currentBreakTime: [first - 1, 9, 0, 0] }
      })
    }
  }

  sessionInc = () => {
    const current = this.state.setSessionTime;
    const first = current[0];
    const second = current[1];
    if (first >= 6) {
      return;
    } else if (second !== 9) {
      this.setState((state, props) => {
        return { setSessionTime: [first, second + 1],
          currentSessionTime: [first, second + 1, 0, 0] }
      });
    } else {
      this.setState((state, props) => {
        return { setSessionTime: [first + 1, 0],
          currentSessionTime: [first + 1, 0, 0, 0] }
      })
    }
  }

  sessionDec = () => {
    const current = this.state.setSessionTime;
    const first = current[0];
    const second = current[1];
    if (first === 0 && second <= 1) {
      return;
    } else if (second !== 0) {
      this.setState((state, props) => {
        return { setSessionTime: [first, second - 1],
          currentSessionTime: [first, second - 1, 0, 0] }
      });
    } else {
      this.setState((state, props) => {
        return { setSessionTime: [first - 1, 9],
          currentSessionTime: [first - 1, 9, 0, 0] }
      })
    }
  }

  getGoingOrStop = () => {
    const wasCounting = this.state.isCounting;
    const wasPaused = this.state.isPaused;
    if (!wasCounting) {
      const countdown = setInterval(this.timerLogic, 1000);
      this.setState({
        isCounting: true,
        isPaused: false,
        countdown: countdown
      });
    } else if (wasPaused) {
      const countdown = setInterval(this.timerLogic, 1000);
      this.setState({
        isPaused: false,
        countdown: countdown
      });
    } else if (!wasPaused) {
      clearInterval(this.state.countdown);
      this.setState({
        isPaused: true,
        countdown: {}
      })
    }
  }

  timerLogic = () => {
    if (this.state.type === "Session") {
      if (this.state.currentSessionTime[3] !== 0) {
        this.setState((state, props) => {
          return {
            currentSessionTime: [state.currentSessionTime[0], state.currentSessionTime[1], state.currentSessionTime[2], state.currentSessionTime[3] - 1]
          }
        });
      } else if (this.state.currentSessionTime[2] !== 0) {
        this.setState((state, props) => {
          return {
            currentSessionTime: [state.currentSessionTime[0], state.currentSessionTime[1], state.currentSessionTime[2] - 1, 9]
          }
        });
      } else if (this.state.currentSessionTime[1] !== 0) {
        this.setState((state, props) => {
          return {
            currentSessionTime: [state.currentSessionTime[0], state.currentSessionTime[1] - 1, 5, 9]
          }
        });
      } else if (this.state.currentSessionTime[0] !== 0) {
        this.setState((state, props) => {
          return {
            currentSessionTime: [state.currentSessionTime[0] - 1, 9, 5, 9]
          }
        });
      } else {
        this.switchTracks();
      }
    } else if (this.state.type === "Break") {
      if (this.state.currentBreakTime[3] !== 0) {
        this.setState((state, props) => {
          return {
            currentBreakTime: [state.currentBreakTime[0], state.currentBreakTime[1], state.currentBreakTime[2], state.currentBreakTime[3] - 1]
          }
        });
      } else if (this.state.currentBreakTime[2] !== 0) {
        this.setState((state, props) => {
          return {
            currentBreakTime: [state.currentBreakTime[0], state.currentBreakTime[1], state.currentBreakTime[2] - 1, 9]
          }
        });
      } else if (this.state.currentBreakTime[1] !== 0) {
        this.setState((state, props) => {
          return {
            currentBreakTime: [state.currentBreakTime[0], state.currentBreakTime[1] - 1, 5, 9]
          }
        });
      } else if (this.state.currentBreakTime[0] !== 0) {
        this.setState((state, props) => {
          return {
            currentBreakTime: [state.currentBreakTime[0] - 1, 9, 5, 9]
          }
        });
      } else {
        this.switchTracks();
      }
    }
  }

  switchTracks = () => {
    const type = this.state.type;
    document.getElementById("beep").currentTime = 0;
    document.getElementById("beep").play();
    if (type === "Break") {
    this.setState((state, props) => ({
      type: "Session",
      currentBreakTime: state.setBreakTime.concat([0, 0])
    }))
  } else if (type === "Session") {
    this.setState((state, props) => ({
      type: "Break",
      currentSessionTime: state.setSessionTime.concat([0, 0])
    }))
  }
  }

  reset = () => {
    clearInterval(this.state.countdown);
    document.getElementById("beep").currentTime = 0;
    this.setState((state,props) => ({
      isCounting: false,
      isPaused: true,
      currentSessionTime: state.setSessionTime.concat([0, 0]),
      currentBreakTime: state.setBreakTime.concat([0, 0]),
      countdown: {},
      type: "Session"
    }))
  }

  render() {
    return (
      <main id="app">
        <header>
        <h1 id="title">Productivity Clock</h1>
        </header>
        <Timer isCounting={this.state.isCounting} type={this.state.type} setSessionTime={this.state.setSessionTime} setBreakTime={this.state.setBreakTime} getGoingOrStop={this.getGoingOrStop} currentBreakTime={this.state.currentBreakTime} currentSessionTime={this.state.currentSessionTime} reset={this.reset} />
        <button type="button" id="options-button" className="btn btn-outline-light" onClick={this.visToggle} ><i className={this.state.visible ? "fas fa-minus" : "fas fa-plus"}></i> &nbsp;Options</button>
        <Options isCounting={this.state.isCounting} setSessionTime={this.state.setSessionTime} setBreakTime={this.state.setBreakTime} visible={this.state.visible} sessionDec={this.sessionDec} sessionInc={this.sessionInc} breakDec={this.breakDec} breakInc={this.breakInc} />
      </main>
    );
  }
}

export default App;
