import React, { Component } from 'react';
import beep from '../static assets/beep.mp3';
import propTypes from 'prop-types';

class Timer extends Component {
    

    render() {
        let timeArr = [];
        !this.props.isCounting 
        ? timeArr = this.props.setSessionTime.concat([0, 0])
        : this.props.type === "Session"
        ? timeArr = this.props.currentSessionTime
        : timeArr = this.props.currentBreakTime;
        return (
            <div id="timer">
                <audio src={beep} id="beep"></audio>
                <h2 id="timer-label">{this.props.type}</h2>
                <div id="time-left">
                   <p id="counter">{`${timeArr[0]}${timeArr[1]}:${timeArr[2]}${timeArr[3]}`}</p>
                </div>
                <div id="buttons">
                    <button type="button" id="start_stop" className="btn btn-outline-light" onClick={this.props.getGoingOrStop}>Start/Stop</button>
                    <button type="button" id="reset" className="btn btn-outline-light" onClick={this.props.reset}>Reset</button>
                </div>
            </div>
        )
    }
}

Timer.propTypes = {isCounting: propTypes.bool.isRequired, type: propTypes.string.isRequired, setSessionTime: propTypes.array.isRequired, setBreakTime: propTypes.array.isRequired, currentSessionTime: propTypes.array.isRequired, currentBreakTime: propTypes.array.isRequired, getGoingOrStop: propTypes.func.isRequired, reset: propTypes.func.isRequired};

export default Timer
